  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
}

.toggle-mnu:focus {
  outline: none;
}

.sandwich {
  width: 32px;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 200;
  top: 50%;
  transform: translateY(-50%);
}

.sw-topper {
  display: block;
  position: relative;
  transform-origin: left top;
  width: 32px;
  height: 3px;
  background: #3E3F43;
  border: none;
  transition: transform 0.5s;
  margin-bottom: 5px;
}

@media only screen and (max-width: 599px) {
  .sw-topper {
    margin-bottom: 4px;
  }
}

.sw-bottom {
  overflow: hidden;
  display: block;
  position: relative;
}

.sw-bottom__inner {
  display: block;
  position: relative;
  width: 32px;
  height: 3px;
  transform-origin: center center;
  background: #3E3F43;
  border: none;
  transition: transform 0.3s ease-in-out, opacity .5s ease-in-out;
  margin-bottom: 5px;
}

@media only screen and (max-width: 599px) {
  .sw-bottom__inner {
    margin-bottom: 4px;
  }
}

.sw-footer {
  display: block;
  position: relative;
  width: 32px;
  height: 3px;
  transform-origin: left;
  background: #3E3F43;
  border: none;
  transition: transform 0.5s;
}

.sandwich.is-active .sw-topper {
  transform: translate(0, -3px) rotate(45deg);
}

.sandwich.is-active .sw-bottom__inner {
  opacity: 0;
  transform: translateX(-105%);
}

