.home-offer__item {
  position: relative;
  padding-left: 31px;
  font-weight: 200;
  line-height: 1;
}

.home-offer__item:not(:last-child) {
  margin-bottom: 12px;
}

.home-offer__item:before {
  content: "";
  position: absolute;
  width: 14px;
  height: 3px;
  border-radius: 115px;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: #C8C8C8;
}

.home-form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin-top: 32px;
  color: #929292;
}

@media only screen and (max-width: 599px) {
  .home-form {
    margin-top: 20px;
  }
}

.home-form.novalidate .home-error {
  display: flex;
}

.home-form-group {
  width: 100%;
  text-align: left;
}

.home-form-group:not(:last-child) {
  margin-bottom: 16px;
}

.home-input {
  position: relative;
}

.home-input .form-error {
  display: none;
}

.home-input:not(:last-child) {
  margin-bottom: 8px;
}

.home-input input {
  width: 100%;
  font-size: 12px;
  font-family: "Yanone Kaffeesatz", Helvetica, sans-serif;
  font-weight: 300;
  padding: 13px 18px 11px;
  border: 2px solid #C8C8C8;
}

.home-input input:focus::placeholder {
  font-size: 0;
}

.home-input__icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 13px;
}

.home-input__icon .icon {
  display: block;
  font-size: 13px;
}

.home-input.has-error {
  color: #E29191;
}

.home-input.has-error input {
  color: #E29191;
  border-color: #E29191;
}

.home-input.has-error input::placeholder {
  color: #E29191;
}

.home-input_icon input {
  padding-left: 38px;
}

.home-btn {
  margin-top: 26px;
}

.home-error {
  margin: 0 0 11px 14px;
  vertical-align: middle;
  line-height: 1.1666;
  display: flex;
  align-items: center;
  color: #E29191;
  display: none;
}

.home-error .icon {
  margin-right: 11px;
}

