body {
	color: $black;
	font-family: $yanone;
	@include font-smoothing;
	@include f(20px,300,1.55);
	min-width: 320px;
	background: $white;
	@include sm {
		font-size: 13px;
		line-height: 1.50; }
	&.is-overflow-hidden {
		overflow: hidden;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%; } }
